<template>
    <view id="publishBox" class="box-bottom">

        <view style="position: relative;">

            <textarea :adjust-position="false" @focus="focus" :cursor-spacing="$kt.util.rpxToPx('40')" :auto-height="true"
                :disabled="isPublishing" class="box-bottom-textarea" v-model="sendRequestParam.content" placeholder="说点什么吧~"
                @input="input" placeholder-class="textarea-placeholder" />

            <image v-if="sendRequestParam.content.length > 0 && !isPublishing" class="btn"
                @click="publish(sendRequestParam.content, 'text')" style="
  position: absolute;
width: 50rpx;
height: 50rpx;
bottom: 15rpx;
right: 20rpx;
" :src="$kt.file.byPath('image/icon/send.svg')" mode="widthFix" />

            <image v-else class="btn" style="
  position: absolute;
opacity:0.5;
width: 50rpx;
height: 50rpx;
bottom: 15rpx;
right: 20rpx;
" :src="$kt.file.byPath('image/icon/send.svg')" mode="widthFix" />
        </view>
        <view style="height: 10rpx;"></view>

        <view>
            <u-grid :col="5">
                <u-grid-item @click="chooseImage">
                    <image class="box-bottom-icon" :src="$kt.file.byPath('/image/chat/image.svg')"></image>
                </u-grid-item>
                <u-grid-item @click="chooseVideo">
                    <image class="box-bottom-icon" :src="$kt.file.byPath('/image/chat/video.svg')"></image>
                </u-grid-item>
                <u-grid-item @click="openVoiceBox">
                    <image class="box-bottom-icon" :src="$kt.file.byPath('/image/chat/voice.svg')"></image>
                </u-grid-item>
                <u-grid-item>
                    <image class="box-bottom-icon" :src="$kt.file.byPath('/image/chat/expression.svg')"></image>
                </u-grid-item>
                <u-grid-item @click="openGiftPopup()">
                    <image style="width: 40rpx;height:40rpx;" class="box-bottom-icon"
                        :src="$kt.file.byPath('/image/icon/gift.svg')"></image>
                </u-grid-item>
            </u-grid>
            <voiceBox v-if="voiceBoxOpen" @submit="voiceBoxSubmit"></voiceBox>
        </view>
        <view
        style="height: 15rpx;"
        ></view>
        <view
        style="
        position: relative;
        background-color: #fff;width:100%;"
         :style="{
                height: keyHeight + 'px'
            }">
            <image
            v-show="keyHeight > $kt.util.rpxToPx('100')"
            style="
            position: absolute;
            width: 100rpx;
            height: 100rpx;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            "
            :src="$kt.file.byPath('/image/icon/keyboard.svg')"
            ></image> 
        </view>
        <gift-popup ref="giftPopup"></gift-popup>

    </view>
</template>

<script>
import voiceBox from "./inChatInput/voiceBox.vue";

export default {
    components: {
        voiceBox
    },
    props: {
        room: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    data() {
        return {
            voiceBoxOpen: false,
            sendRequestParam: {
                roomId: "",
                content: "",
                typeCode: "text",
                otherUserId: ""
            },
            isPublishing: false,
            keyHeight: 0
        }
    },
    mounted() {
        if (this.sendRequestParam.roomId != null) {
            this.sendRequestParam.roomId = this.room.id;
            this.sendRequestParam.otherUserId = this.room.otherUser.userId;
        }
        // 监听键盘高度变化
        uni.onKeyboardHeightChange( (res)=> {
            // 键盘高度发生变化时触发的回调函数
            this.keyHeight = res.height;
            this.$forceUpdate();
            this.$emit("change");
            setTimeout(() => {
                this.$emit("change");
            }, 100);
            console.log('键盘高度：', this.keyHeight);
        });

    },
    methods: {
        focus() {
            this.voiceBoxOpen = false;
            this.$emit("change");
        },
        openGiftPopup() {
            this.$refs.giftPopup.open({
                toUserId: this.room.otherUser.userId
            });
        },
        openVoiceBox() {
            // 查看是否有录音权限
            uni.getSetting({
                success: (res) => {
                    if (!res.authSetting['scope.record']) {
                        uni.authorize({
                            scope: 'scope.record',
                            success: () => {
                                this.voiceBoxOpen = !this.voiceBoxOpen;
                                setTimeout(() => {
                                    this.$emit("change");
                                }, 100);
                            },
                            fail: () => {
                                uni.showModal({
                                    title: '提示',
                                    content: '您未授权录音权限，是否去设置？',
                                    success: (res) => {
                                        if (res.confirm) {
                                            uni.openSetting({
                                                success: (res) => {
                                                    if (res.authSetting['scope.record']) {
                                                        this.voiceBoxOpen = !this.voiceBoxOpen;
                                                        setTimeout(() => {
                                                            this.$emit("change");
                                                        }, 100);
                                                    }
                                                }
                                            });
                                        }
                                    }
                                });
                            }
                        });
                    } else {
                        this.voiceBoxOpen = !this.voiceBoxOpen;
                        setTimeout(() => {
                            this.$emit("change");
                        }, 100);
                    }
                }
            });
            // this.voiceBoxOpen = !this.voiceBoxOpen;
            // setTimeout(() => {
            //     this.$emit("change");
            // }, 100);
        },
        voiceBoxSubmit(file) {
            this.voiceBoxOpen = false;
            setTimeout(() => {
                this.$emit("change");
            }, 100);
            this.uploadFile(file.path, "voice", file.duration);

        },
        chooseImage() {
            uni.chooseImage({
                count: 1,
                success: (res) => {
                    var tempFilePaths = res.tempFilePaths;
                    for (var i = 0; i < tempFilePaths.length; i++) {
                        this.uploadFile(tempFilePaths[i], "image");
                    }
                }
            });
        },
        chooseVideo() {
            uni.chooseMedia({
                count: 1,
                sourceType: ['album', 'camera'],
                mediaType: ['video'],
                maxDuration: 60,
                camera: 'back',
                success: (res) => {
                    var tempFilePath = res.tempFiles[0].tempFilePath;
                    this.uploadFile(tempFilePath, "video");
                }
            });
        },
        async uploadFile(filePath, typeCode, duration) {
            uni.showLoading({
                title: '正在上传'
            });
            await new Promise((resolve, reject) => {
                this.$kt.request.uploadFile({
                    name: "file",
                    formData: {
                        groupCode: "chat"
                    },
                    filePath,
                    success: (res) => {
                        var json = JSON.parse(res.data);
                        this.publish(json.data.id, typeCode, duration);
                        setTimeout(() => {
                            uni.hideLoading();
                        }, 2000);
                        resolve();
                    },
                    fail: (res) => {
                        reject();
                    }
                });
            });

        },
        input(e) {
            this.voiceBoxOpen = false;
            this.sendRequestParam.content = e.detail.value;
        },
        publish(content, type, duration) {
            if (this.room.typeCode == "privateChat") {
                this.chatPrivate(content, type, duration);
            }
        },
        chatPrivate(content, typeCode, duration) {

            return new Promise((resolve, reject) => {

                if (this.isPublishing) {
                    // 如果正在发送中，不允许再次发送
                    return falsee;
                }

                this.isPublishing = true;

                this.$kt.request.request({
                    api: this.$api.business.ovo.chat.chatPrivate,
                    data: {
                        otherUserId: this.sendRequestParam.otherUserId,
                        content,
                        typeCode,
                        duration
                    },
                    stateSuccess: (res) => {
                        this.sendRequestParam.content = "";
                        var json = res.data;
                        json.ovoUserOfSend = this.ovoUserInfo;
                        uni.$emit("newChat:roomId:" + this.room.roomId, json);
                        this.isPublishing = false;
                        this.$forceUpdate();
                        this.$emit("publish", json);
                        setTimeout(() => {
                            this.$emit("change");
                        }, 200);
                        resolve();
                    }
                });
            });

        },
    },
    // 监听sendRequestParam.content的变化
    watch: {
        sendRequestParam: {
            handler: function (val, oldVal) {
                console.log("变化了");
                this.$emit("change");
            },
            deep: true
        }
    }
}
</script>


<style lang="scss" scoped>
.box-bottom {
    position: relative;
    background-color: #fff;
    bottom: 0;
    width: 100%;
    padding: 15rpx 15rpx 0 15rpx;
    box-sizing: border-box;

    .box-bottom-textarea {
        max-height: 200rpx;
        padding: 20rpx;
        font-size: 30rpx;
        width: calc(100% - 150rpx);
        background-color: #f0f0f0;
        border-radius: 20rpx;
    }

    .box-bottom-icon {
        width: 50rpx;
        height: 50rpx;
    }
}

.btn:active {
    transform: scale(0.9);
}
</style>